.content-container {
  padding: 2rem 16px;
  .content {
    p {
      font-size: 16px;
    }
    .radio-group {
      display: flex;
      align-items: center;
      margin-bottom: 16px;
      p{
        min-width: 200px;
      }
      label {
        font-size: 12px;
        margin-right: 16px;
        cursor: pointer;
      }
      input {
        cursor: pointer;
      }
    }
    .input-group {
      .row {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
      }
      label {
        font-size: 12px;
        margin-right: 16px;
      }
      .account-col{
        width: 40%;
      }
      .operation-col{
        width: 10%;
        display: flex;
        justify-content: center;
        // temporary
        input[type="checkbox"]{
          -webkit-appearance: checkbox;
        }
      }
    }
  }
}

.operations {
  border-top: 1px solid black;
}

button {
  background-color: black;
  color: white;
  padding: 4px 8px;
  border-radius: 2px;
  min-width: 80px;
  margin-right: 8px;
  &:disabled {
    background-color: rgb(138, 138, 138);
  }
}
.btn {
  display: flex;
  justify-content: space-between;
  margin-top: 16px;
  margin-bottom: 16px;
}
